import React, { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import styles from "../Styles";

const ConnectWallet = () => {
  const navigate = useNavigate();

  useEffect(() => {
    connectPhantomWallet();
    // eslint-disable-next-line
  }, []);

  const connectPhantomWallet = async () => {
    try {
      const { solana } = window;
      if (solana) {
        const resp = await window?.solana?.connect();
        console.log(
          "Connected with wallet address:",
          resp.publicKey.toString()
        );
        localStorage.setItem("OwnerWallet", resp.publicKey.toString());
        navigate("/nft-items");
      } else {
        window?.open("https://phantom.app/");
      }
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <>
      <h1 className={`${styles.connectWalletTitle}`}>Upgrade Your Sovereign</h1>
      <div className="grid grid-cols-2 gap-4 ">
        <div className={`${styles.connectWalletFillCard}`}>Your Wallet</div>
        <div className={`${styles.connectWalletCard}`}>Upgrade</div>
      </div>
      <div className="flex justify-center items-center pt-20">
        <div className="connectwallet-card px-20 py-10 text-center">
          <h1 className={`${styles.connectWalletTitle}`}>
            Please Connect Your Wallet
          </h1>
          <button
            className="btn-fill text-2xl p-3"
            onClick={() => connectPhantomWallet()}
          >
            connect wallet
          </button>
        </div>
      </div>
    </>
  );
};

export default ConnectWallet;
